function timer() {
    /// 

    let deadline = '2019-08-01';

    //        
    const  timeRemaining = endtime => {
      let t = Date.parse(endtime) - Date.parse(new Date()), //(  parse)
        seconds = Math.floor((t / 1000) % 60), //    ( Math.floor),        60
        minutes = Math.floor((t / 1000 / 60) % 60), // ---    60
        hours = Math.floor(t / 1000 / 60 / 60); // 
      // Math.floor((t/1000/60/60) % 24);    
      
      return {
        'total' : t,
        'hours' : hours,
        'minutes' : minutes,
        'seconds': seconds
      };
    };
  
  //    
  
  const setTime = (id, endtime) => {
    //   
    let timer = document.getElementById(id),
      hours = timer.querySelector('.hours'),
      minutes = timer.querySelector('.minutes'),
      seconds = timer.querySelector('.seconds'),
      timeInterval = setInterval(timeUpdate, 1000);
  
      ///    
      function  timeUpdate() {
        //        timeRemaining 
        let r = timeRemaining(endtime);
        hours.textContent = r.hours;
        minutes.textContent = r.minutes;
        seconds.textContent = r.seconds;
        if(r.total <= 0) {
          clearInterval(timeInterval);
          hours.textContent = '00';
          minutes.textContent = '00';
          seconds.textContent = '00';
        }
        if (r.seconds <= 9) {
          seconds.textContent =  '0' + r.seconds;
        }
        if (r.minutes <= 9) {
          minutes.textContent =  '0' + r.minutes;
        }
        if (r.hours <= 9) {
          hours.textContent =  '0' + r.hours;
        }
      }
  
  };
  
  //       ID          
  setTime('timer', deadline);
}
module.exports = timer;